<template>
    <div class="header">
        <h1>PASTEL</h1>
        <div class="h_box">
            <input class="h_bt" type="button" v-for="(m, h_dex) in menu" :key="h_dex" :value="m" @click="m_click(h_dex)">
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
export default {
    // name : 'header_1',
    data() {
        return {
            menu : ['MAIN', 'COLOR_LIST'],
            count : '0',
            resize_w : 0,
            resize_h : 0,
        }
    },
    methods :{
        m_click(dex) {
            var router = this.$router;
            if(dex == 0) {
                router.push('/')    
            }else if(dex == 1) {
                router.push('/content1')    
            }
        },
        
        resize_event() { //resize 이벤트
            this.resize_w = window.innerWidth;
            this.resize_h = window.innerHeight;
            if(this.resize_w > 1024) {
                this.alert_TF = true;
            }else if(this.resize_w < 1024) {
                this.alert_TF = false;
            }
        },

        scroll_event() { //scrollTop 이벤트
            var sctop = $(window).scrollTop();
            var header = document.querySelector('.header')
            var h_TF = header.classList.contains('h_E')
            if(sctop > 100 && this.resize_w < 1024) {
                header.classList.add('h_E')
            }else if(sctop == 0 && this.resize_w < 1024 && h_TF == true) {
                header.classList.remove('h_E')
            }
        },
    },
    mounted() {
        window.addEventListener('resize', this.resize_event); //resize
        this.resize_w = window.innerWidth;
        window.addEventListener('scroll', this.scroll_event); //scroll
    },

    beforeDestory() {
        window.removeEventListener('resize', this.resize_event); //resize
        window.addEventListener('scroll', this.scroll_event); //scroll
    },
    watch:{
        '$route' (to, from) { // eslint-disable-line no-unused-vars
            var h_put = document.querySelectorAll('.h_bt')
            for (var u = 0; u < h_put.length; u++) {h_put[u].classList.remove("h_E")}
            if(to.name == 'main') {
                h_put[0].classList.add("h_E")
            }else if(to.name == 'content1') {
                h_put[1].classList.add("h_E")
            }
        }
    }

}

</script>

