<template>
    <div class="main_p">
        <div class="banner">
            <!-- <img :src="require(`../img/main_page1.png`)" alt=""> -->
            <p>PASTEL <em> PALETTE</em></p>
            <div class="mc_box">
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
            </div>
            <!-- <img src="../img/main3.png" alt=""> -->
        </div>
        <h1>오늘은 이런 색 어때요 ?</h1><p>{{c_txt}}</p>
        <div class="h_con">
        <template v-for="(color, indexs) in m_clist">
            <div class="hex_box" @click="link_c(color)" :key="indexs" v-if="color.tag.join().toLowerCase().includes(dice)">
                <router-link :to="{name: 'content1', params : {dice_color : color.hex }}"></router-link>
                <div class="hex_card">
                    <img :src="require(`../img/${color.hex}.png`)" alt="">
                    <p>{{ color.hex }}</p>
                </div>
            </div>
        </template>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import list from './list.js'
export default {
    // name : 'main',
    data() {
        return {
            m_clist : list,
            dice : "",
            c_txt : "",
        }
    },
    mounted() {
        // console.log("새로고침");
        const random = Math.floor(Math.random() * 7); //0~6
        if(random == 0) {
            this.dice = 'red'
            this.c_txt = "“빨간색은 스펙트럼의 양쪽에 있기 때문에 감정과 연관되는 흥미로운 색상이다.”"
        }else if(random == 1) {
            this.dice = 'blue'
            this.c_txt = '“다른 모든 색을 뛰어넘는 빛나고 아름답고 완벽한 색. 어느 누구도 이 색에 대해 말할 수 없고, 이것으로 아무것도 할 수 없다. 이 색의 질은 여전히 독보적으로 뛰어나다.”'
        }else if(random == 2) {
            this.dice = 'purple',
            this.c_txt = '“보라색은 마지막까지 상대방을 믿고 서로서로 사랑하자는 뜻이에요.”'
        }else if(random == 3) {
            this.dice = 'brown'
            this.c_txt = '“가족이나 친구를 소중히 여기는 안정적인 사람의 색.”'
        }else if(random == 4) {
            this.dice = 'green'
            this.c_txt = '“친애하는 친구여, 모든 이론은 회색 빛 이지만, 인생의 황금 나무는 초록이라네”'
        }else if(random == 5) {
            this.dice = 'beige'
            this.c_txt = '“소설의 처음 나왔던 세련된 색의 이름.”'
        }else if(random == 6) {
            this.dice = 'yellow'
            this.c_txt = '“태양과 햇빛을, 나는 달리 표현 할 수가 없어 노란색, 옅은 유황색, 연한 황금빛 레몬색이라 부른다. 이 얼마나 아름다운 노랑인가!”'
        }
        var k = []; //새로운 배열로 선언
        var mc_box = document.querySelectorAll('.mc_li');
        for (var ss = 0; ss < 6; ss++) {
            var ii = Math.floor(Math.random() * this.m_clist.length);
            if (k.indexOf(ii) === -1) { //현재 k중에 ii가 존재하지않으면 -1를 출력한다. 동일 시 진행
                k.push(ii) //k에 ii배열을 맨 뒤에 추가한다
                $(mc_box[ss]).css({
                    'backgroundColor' : this.m_clist[k[ss]].hex
                })
            }else{ //존재할 시 뒤로 보내서 한번더 실행한다
                ss--
            }
        }
        
        

    },
    methods: {
        link_c(color) {
            this.$router.push({name: 'content1', params : {dice_color : color.hex.toLowerCase()}})
        },
            
    }
}
</script>
