<template>
<div>
    <div class="content1">
        <input class="con1_search" type="text" placeholder="SEARCH" v-model="search">
        <div class="a_con">
            <template v-for="(color, indexs) in c_list">
                <div class="hex_box" @click="hex_click(indexs)" :key="indexs" v-if="color.tag.join().includes(search.toLowerCase())">
                    <div class="hex_card">
                        <img :src="require(`../img/${color.hex}.png`)" alt="">
                        <p>{{ color.hex }}</p>
                    </div>
                </div>
            </template>
        </div>
    </div>
    <div class="alert" v-show="alert_TF">
        <div class="a_img"><img :src="require(`../img/${c_name}.png`)" alt=""></div>
        <div class="a_txt">
            <p class="a_name">{{c_name}}</p>
            <div class="rgb">
                <div class="rgb_c rgb_r">{{r}} ({{r_par}}%)</div>
            </div>
            <div class="rgb">
                <div class="rgb_c rgb_g">{{g}} ({{g_par}}%)</div>
            </div>
            <div class="rgb">
                <div class="rgb_c rgb_b">{{b}} ({{b_par}}%)</div>
            </div>
            <input class="a_put1" type="button" value="HEX COPY" @click="copy(c_name)">
            <input class="a_put2" type="button" value="Check the similar hex" @click="similar">
            <div class="close" @click="close"><span></span><span></span></div>
        </div>
    </div>
</div>
</template>
<script>
import $ from 'jquery';
import list from './list.js'
export default {
    // name : 'content1',
    data() {
        return {
            search : "",
            c_list : list,
            c_name : 'none',
            alert_TF : true,
            r : 0,
            g : 0,
            b : 0,
            r_par : 0,
            g_par : 0,
            b_par : 0,
            sim_num : 0,
            resize_w : 0,
            resize_h : 0,

        }
    },
    methods : {
        hex_click(indexs) {
            this.alert_TF = true;
            this.c_name = this.c_list[indexs].hex;
            this.r = this.c_list[indexs].r;
            this.g = this.c_list[indexs].g;
            this.b = this.c_list[indexs].b;
            this.r_par = (this.r *100 / (this.r + this.g + this.b)).toFixed(2);
            this.g_par = (this.g *100 / (this.r + this.g + this.b)).toFixed(2);
            this.b_par = (this.b *100 / (this.r + this.g + this.b)).toFixed(2);
            $('.rgb_c').removeAttr('style');
            $('.rgb_r').stop().animate({'width' : this.r_par + '%'})
            $('.rgb_g').stop().animate({'width' : this.g_par + '%'})
            $('.rgb_b').stop().animate({'width' : this.b_par + '%'})
            $('.a_name').css({'color' : this.c_name})
            this.sim_num = indexs;
        },

        copy(co) {
            const c_input = document.createElement('input');
            c_input.setAttribute("value", co);
            document.body.appendChild(c_input);
            c_input.select();
            document.execCommand('copy');
            document.body.removeChild(c_input);
        },
        similar() {
            this.search =  this.c_list[this.sim_num].tag[1];
            this.alert_TF = false;
        },
        resize_event() { //resize 이벤트
            this.resize_w = window.innerWidth;
            this.resize_h = window.innerHeight;
            if(this.resize_w > 1024) {
                this.alert_TF = true;
            }else if(this.resize_w < 1024) {
                this.alert_TF = false;
            }
        },
        scroll_event() { //scrollTop 이벤트
            var sctop = $(window).scrollTop();
            var header = document.querySelector('.header')
            var h_TF = header.classList.contains('h_E')
            if(sctop > 100 && this.resize_w < 1024) {
                header.classList.add('h_E')
            }else if(sctop == 0 && this.resize_w < 1024 && h_TF == true) {
                header.classList.remove('h_E')
            }
        },
        close() {
            this.alert_TF = false;
        }

    },
    mounted() {
        if(this.$route.params.dice_color != undefined) {
            this.search = this.$route.params.dice_color
        }
        window.addEventListener('resize', this.resize_event); //resize
        this.resize_w = window.innerWidth;
        if(this.resize_w > 1024) {
            this.alert_TF = true;
        }else if(this.resize_w < 1024) {
            this.alert_TF = false;
        }

        window.addEventListener('scroll', this.scroll_event); //scroll
    },
    beforeDestory() {
        window.removeEventListener('resize', this.resize_event); //resize

        window.addEventListener('scroll', this.scroll_event); //scroll
    },

}
</script>